@import url(https://fonts.googleapis.com/css?family=Rubik:400,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rubik:400,500&display=swap);
body {
	margin: 0;
	font-family: 'Rubik', sans-serif;
	font-size: 18px;
}

code {
	font-family: 'Rubik', sans-serif;
}

:root {
	--soft-blue: hsl(231, 69%, 60%);
	--soft-red: hsl(0, 94%, 66%);
	--grayish-blue: hsl(229, 8%, 60%);
	--very-dark-blue: hsl(229, 31%, 21%);
}

* {
	box-sizing: border-box;
}

main,
.main {
	padding: 0 40px 0 90px;
	height: 100%;
	display: flex;
	flex-direction: column;
}

section {
	margin-bottom: 90px;
}

.sectionTitles {
	font-weight: 500;
	font-size: 2.3rem;
}

.sectionHeader h3 {
	font-weight: 500;
	font-size: 1.2em;
	text-align: center;
}

.sectionHeader .caption {
	text-align: center;
}

.caption {
	color: hsl(229, 8%, 60%);
	color: var(--grayish-blue);
	font-size: 0.8em;
	line-height: 1.5em;
	max-width: 450px;
	min-width: 300px;
	margin: 20px auto;
}

button {
	font-weight: 500;
	font-size: 0.6em;
	padding: 12px 10px;
	border: 2px solid transparent;
	border-radius: 3px;
	box-shadow: 0px 5px 10px rgba(199, 198, 238, 0.664);
	letter-spacing: 0.1em;
}

button.purple-bg {
	background-color: hsl(231, 69%, 60%);
	background-color: var(--soft-blue);
	color: #fff;
}

button.purple-bg:hover {
	border: 2px solid hsl(231, 69%, 60%);
	border: 2px solid var(--soft-blue);
	background-color: #fff;
	color: hsl(229, 31%, 21%);
	color: var(--very-dark-blue);
}

button.grey-bg:hover {
	border: 2px solid hsl(229, 31%, 21%);
	border: 2px solid var(--very-dark-blue);
	background-color: #fff;
}

button:hover {
	cursor: pointer;
}

@media only screen and (max-width: 856px) {
	main,
	.main {
		padding: 0 29px;
		margin: 0;
	}
}



.navContainer {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 30px 0;
	font-family: 'Rubik', sans-serif;
}

nav {
	display: block;
	align-content: flex-end;
}

nav ul li {
	list-style: none;
	display: inline-block;
	padding: 10px 18px;
	font-size: 0.7rem;
	letter-spacing: 0.1em;
	text-transform: uppercase;
}

nav a,
nav a:active,
nav a:visited {
	text-decoration: none;
	color: var(--very-dark-blue);
}

nav a:hover {
	color: var(--soft-red);
}

.login {
	color: #fff;
	background-color: var(--soft-red);
	padding: 10px 30px;
	text-transform: uppercase;
	font-size: 0.7rem;
	letter-spacing: 0.1em;
	border: 2px solid var(--soft-red);
	border-radius: 4px;
	box-shadow: 0px 5px 10px rgba(199, 198, 238, 0.664);
}

button.login:hover {
	color: var(--soft-red);
	border: 2px solid var(--soft-red);
	background-color: #fff;
}

.responsive-menu {
	display: none;
}

@media only screen and (max-width: 1023px) {
	.responsive-menu {
		display: inline-block;
		z-index: 999;
		width: 40px;
		height: 40px;
		background-color: transparent;
		outline: none;
		border: none;
	}

	nav {
		position: absolute;
		right: 0px;
		top: 0px;
		background-color: var(--very-dark-blue);
		text-align: center;
		height: 100%;
		width: 100%;
		opacity: 0.97;
	}

	nav ul {
		padding: 60px 50px;
	}

	nav ul li a,
	.login {
		color: #f2f2f2;
		background-color: transparent;
		text-transform: uppercase;
		font-size: 1em;
	}

	nav ul li {
		display: block;
		border-bottom: 1px solid var(--grayish-blue);
		padding: 20px;
	}

	nav ul li:nth-last-child(1) {
		border: none;
	}

	.login {
		border-color: #f2f2f2;
		box-shadow: none;
	}

	nav a,
	nav a:active,
	nav a:visited {
		text-decoration: none;
		color: #f2f2f2;
	}

	.hOne,
	.hTwo,
	.hThree {
		display: block;
		position: relative;
		border-bottom: 4px solid var(--very-dark-blue);
		width: 22px;
		height: 8px;
		top: -2px;
		opacity: 1;
		-webkit-transition: all 200ms;
		transition: all 200ms;
	}

	.hOneAnimate,
	.hTwoAnimate {
		border-color: #f2f2f2;
	}

	.hOneAnimate {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg);
		-webkit-transition: -webkit-transform 200ms;
		transition: -webkit-transform 200ms;
		transition: transform 200ms;
		transition: transform 200ms, -webkit-transform 200ms;
		top: 5px;
		left: 1px;
	}

	.hTwoAnimate {
		-webkit-transform: rotate(-45deg);
		        transform: rotate(-45deg);
		top: -3px;
		right: 2px;
	}

	.hThreeAnimate {
		opacity: 0;
		-webkit-transition: display 200ms;
		transition: display 200ms;
	}
}

section:first-of-type {
	font-weight: 400;
	display: flex;
	box-sizing: border-box;
}

.purpleBox {
	max-height: 20rem;
	min-height: 18rem;
	width: 35vw;
	border-top-left-radius: 8em;
	border-bottom-left-radius: 8em;
	position: absolute;
	right: 0;
	margin-top: 70px;
	align-self: center;
	background-color: var(--soft-blue);
	z-index: -999;
}

section:first-of-type h1 {
	font-weight: 500;
	font-size: 2.30rem;
}

.leftContent {
	display: flex;
	flex-flow: column;
	width: 50%;
	align-self: center;
}

.leftContentWrap {
	align-self: center;
}

.leftContent .caption {
	margin: 0 0 20px 0;
}

.rightContent {
	width: 50%;
	display: flex;
}

.rightContent img {
	max-width: 46vw;
	max-height: auto;
}

section:first-of-type .caption {
	color: var(--grayish-blue);
	font-size: 0.8em;
	line-height: 1.5em;
}

.header-button-wrapper button {
	margin-right: 10px;
}

@media only screen and (max-width: 856px) {
	section:first-of-type {
		flex-direction: column-reverse;
	}

	section:first-of-type .caption {
		line-height: 1.9em;
	}

	.rightContent {
		padding-top: 40px;
		width: 100%;
		height: auto;
		text-align: center;
	}

	.purpleBox {
		width: 76%;
		margin-top: 30px;
		min-height: 200px;
		max-height: 250px;
		text-align: center;
	}

	.rightContent img {
		max-width: 86vw;
		margin: auto;
	}

	.leftContent {
		width: 100%;
		text-align: center;
		align-self: center;
	}
}

@-webkit-keyframes opac {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@keyframes opac {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

.sectionTwoHeader {
	text-align: center;
	max-width: 450px;
	margin: 0 auto;
}

.slide-link-wrapper {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 1px 0;
	border-bottom: 1px solid hsla(229, 8%, 60%, 0.383);
	width: 70%;
	margin: 30px auto;
}

.slide-link-wrapper button {
	padding: 20px 20px;
	margin: -2px 16px;
	border: 3px solid transparent;
	font-size: 0.7em;
	color: var(--grayish-blue);
	letter-spacing: 0.1em;
	background-color: transparent;
	outline: none;
	border: 0px solid transparent;
	border-bottom: 3px solid transparent;
	box-shadow: none;
}

.slide-link-wrapper button:hover {
	color: var(--soft-red);
	cursor: pointer;
}

button.currentSlide {
	border: 0px solid transparent;
	border-bottom: 3px solid red;
}

.slideContainer {
	display: flex;
}

.slideContent {
	justify-content: space-evenly;
	margin: 0 auto;
}

.slideImg,
.slideWordWrap {
	width: 50%;
	align-self: center;
	margin: 0 28px;
}

.slideImg img,
.slideWordWrap {
	-webkit-animation: opac 200ms ease-in;
	        animation: opac 200ms ease-in;
}

.slideImg img {
	min-width: 46%;
	max-width: 100%;
	max-height: auto;
}

.slideWordWrap {
	align-self: center;
}

.slideWordWrap button {
	height: 37px;
	width: 89px;
	box-shadow: 0px 5px 10px rgba(199, 198, 238, 0.664);
	background-color: var(--soft-blue);
	color: #fff;
	border: 2px solid transparent;
	border-radius: 4px;
}

.slideWordWrap button:hover {
	background-color: #fff;
	color: var(--soft-blue);
	border: 2px solid var(--soft-blue);
}

.slideImg .purpleBox {
	-webkit-transform: scaleX(-1);
	        transform: scaleX(-1);
	left: 0;
	margin-top: 85px;
	width: 45vw;
	min-height: 16em;
}

@media only screen and (max-width: 856px) {
	.slide-link-wrapper {
		flex-direction: column;
	}

	.slideContent {
		flex-direction: column;
	}

	.slideImg,
	.slideWordWrap {
		width: 100%;
		text-align: center;
		margin: 0;
	}
}

.card-wrapper {
	display: flex;
	height: 344px;
	justify-content: center;
	flex-wrap: wrap;
}

.cardOne {
	align-self: flex-start;
}

.cardTwo {
	align-self: center;
}

.cardThree {
	align-self: flex-end;
}

.card {
	min-width: 213px;
	max-width: 213px;
	text-align: center;
	margin: 10px 14px;
	padding: 29px 0 19px;
	border-radius: 6px;
	box-shadow: 0px 5px 10px rgba(199, 198, 238, 0.364);
}

.card h4 {
	font-size: 0.83em;
	letter-spacing: 0.03em;
}

.card img {
	width: 37%;
}

img.bgCircle {
	width: 100%;
	padding: 0;
	margin: 15px 0;
}

.card button {
	padding: 11px 26px;
	border-radius: 4px;
}

.card-caption {
	color: var(--grayish-blue);
	font-size: 0.64em;
	margin-top: -12px;
}

@media only screen and (max-width: 856px) {
	.card-wrapper {
		height: 100%;
	}
}

.faqsContainer {
	max-width: 500px;
	min-width: 300px;
	margin: 0 auto;
}

.question {
	color: var(--very-dark-blue);
}

.question span {
	float: right;
}

.answer {
	color: var(--grayish-blue);
	line-height: 1.7em;
	font-size: 0.94em;
}

.answer p {
	margin-top: 25px;
}

.faqsContainer .question:hover {
	color: var(--soft-red);
	cursor: pointer;
}

.faqsContent {
	border-top: 1px solid hsla(229, 8%, 60%, 0.28);
}

.faqsContent:nth-last-child(1) {
	border-bottom: 1px solid hsla(229, 8%, 60%, 0.28);
}

.hide {
	display: none;
}

footer {
	display: flex;
	flex-flow: column;
	background-color: var(--soft-blue);
	margin: 0;
	text-align: center;
	color: #f2f2f2;
}

.footerTop {
	padding: 40px 20px 60px 20px;
}

.footerCaption {
	font-size: 0.6em;
	text-transform: uppercase;
	letter-spacing: 0.3em;
}

.footerHeading {
	font-weight: 400;
}

.contactUs {
	color: #fff;
	background-color: var(--soft-red);
	padding: 12px 15px;
	text-transform: uppercase;
	font-size: 0.7rem;
	letter-spacing: 0.1em;
	border: 2px solid var(--soft-red);
	border-radius: 4px;
	margin-left: 20px;
}

input[type=email] {
	padding: 13px 15px;
	min-width: 300px;
	border: none;
	border-radius: 4px;
	outline: none;
	font-size: 0.8rem;
}

::-webkit-input-placeholder {
	color: #afafafbe;
}

::-moz-placeholder {
	color: #afafafbe;
}

:-ms-input-placeholder {
	color: #afafafbe;
}

::-ms-input-placeholder {
	color: #afafafbe;
}

::placeholder {
	color: #afafafbe;
}

input[type=email]:invalid {
	border: 2px solid var(--soft-red);
}

.footerBottom {
	display: flex;
	background-color: var(--very-dark-blue);
	width: 100%;
	padding: 13px 0 9px 0;
}

.footerBottom ul {
	display: flex;
	justify-content: center;
	list-style: none;
}

.footerBottom a {
	color: #f2f2f2;
	font-size: 0.66em;
	padding-right: 40px;
	text-transform: uppercase;
	text-decoration: none;
	letter-spacing: 0.1em;
}

.footerSocialLinks {
	width: 100%;
}

.socialLinks {
	float: right;
}

.socialLinks img {
	width: 20px;
}

.icon:hover {
	fill: red;
}

@media only screen and (max-width: 768px) {
	.footerBottom {
		flex-direction: column;
		padding-bottom: 40px;
	}

	.links {
		flex-direction: column;
	}

	.socialLinks {
		float: none;
		margin: 0;
	}

	.socialLinks li {
		padding: 0;
	}

	footer li {
		padding: 10px 5px;
	}
}

@media only screen and (max-width: 568px) {
	.contactUs {
		min-width: 300px;
		margin: 11px 0 0 0;
	}
}

